import React, {Component} from 'react'
import {StyleRoot} from 'radium'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'

export default class Layout extends Component {
  render () {
    return (
      <MuiThemeProvider>
        <StyleRoot>
          <div>
            {this.props.children}
          </div>
        </StyleRoot>
      </MuiThemeProvider>
    )
  }
};



