import React, {Component} from 'react'
import Radium from 'radium'
import {Card, CardTitle, CardText} from 'material-ui/Card'
import RaisedButton from 'material-ui/RaisedButton'
import CircularProgress from 'material-ui/CircularProgress'
import ContentAdd from 'material-ui/svg-icons/content/add'
import ContentRemove from 'material-ui/svg-icons/content/remove'
import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'
import Formsy from 'formsy-react'
import {FormsyText} from 'formsy-material-ui/lib'
import ActionAccountBalanceWallet from 'material-ui/svg-icons/action/account-balance-wallet'
import FileFileDownload from 'material-ui/svg-icons/file/file-download'
import FileFileUpload from 'material-ui/svg-icons/file/file-upload'
import moment from 'moment'
import {styles} from './wallet-styles'
import WalletAPI from '../../api/wallet-api'
import _get from 'lodash.get'

const graterThanZeroValidation = {
  graterThanZero: (values, value) => value < 0 ? '    .' : true,
  isNumeric: true
}

class Wallet extends Component {
  state = {
    showPopup: false,
    showPreload: false,
    canSubmit: false,
    money: 0,
    dataLoaded: false,
    sorted: false,
    yourAddress: '',
    walletData: []
  };

  async componentWillMount () {
    this.loadWalletData()
  }

  loadWalletData = () => {
    return this.ensureDataIsLoaded()
          .then((res) => {
            this.setState({
              dataLoaded: true,
              money: res.money,
              yourAddress: res.address,
              walletData: res.transactionHistory
            })
          })
          .catch(e => this.setState({dataLoaded: true, dataLoadingError: e}))
  };

  saveKeys (newWalletKeys) {
    console.log('saving wallet key:', newWalletKeys)
    window.localStorage.setItem('walletKeys', newWalletKeys)
    return newWalletKeys
  }

  async ensureDataIsLoaded () {
    const walletStorage = window.localStorage
    const wif = walletStorage.getItem('walletKeys') || this.saveKeys(await WalletAPI.createNew())
    const currencyState = await WalletAPI.loadBalance(wif)
    const address = await WalletAPI.getAddress(wif)
    const transactionHistory = await WalletAPI.loadTransactionHistory(currencyState, address)
    return {money: currencyState.amount, transactionHistory: transactionHistory, address: address}
  }

  handleClose = () => this.setState({showPopup: false});
  enableButton = () => this.setState({canSubmit: true});
  disableButton = () => this.setState({canSubmit: false});
  showPreload = () => this.setState({showPreload: true});

  showPopup = () => {
    this.showPreload()
    setTimeout(() => {
      this.setState({showPreload: false, showPopup: true})
    }, 2000)
  };

  submitForm = async (data) => {
    const {money, numberWallet, msgTransaction} = data
    this.handleClose()
    this.showPreload()
    const walletStorage = window.localStorage
    const walletKey = walletStorage.getItem('walletKeys')
    await WalletAPI.createNewTransaction(walletKey, numberWallet, money, msgTransaction)
    const transaction = await WalletAPI.createNewTransaction(walletKey, numberWallet, money, msgTransaction)
    console.log('submit form - transaction:', transaction)
    const changedMoney = this.state.money - money
    const currentWallet = this.state.walletData
    currentWallet.push({
      incoming: false,
      money: money,
      number: numberWallet,
      dateTransaction: moment(new Date()).format('DD.MM.YYYY')
    })
    this.setState({money: changedMoney, showPreload: false, WalletData: currentWallet})
    this.addDataToLocalStorage(changedMoney)
  };

  addDataToLocalStorage = (money) => {
    window.localStorage['money'] = money
    window.localStorage['transactionHistory'] = JSON.stringify(this.state.walletData)
  };

  renderMoneyInfo = (styles) => {
    return <div style={styles.moneyInfo}>
      <div style={styles.flexAlignCenterContainer}>
        <span style={styles.walletNumber}>
          FTT&nbsp;{this.state.money}
        </span>
      </div>
    </div>
  };

  renderDialog = (styles) => {
    return <Dialog title=' '
      modal
      open={this.state.showPopup}
    >

      <Formsy.Form ref='form'
        onValid={this.enableButton}
        onInvalid={this.disableButton}
        onValidSubmit={this.submitForm}>
        <div style={styles.inputContainer}>
          <FormsyText
            style={styles.textFieldStyles}
            name='numberWallet'
            floatingLabelText=' '
            required />
          <FormsyText
            style={styles.textFieldStyles}
            name='money'
            type='number'
            floatingLabelText=''
            validations={graterThanZeroValidation}
            required />
          <FormsyText
            style={styles.textFieldStyles}
            name='msgTransaction'
            floatingLabelText=''
            />
        </div>
        <div style={styles.dialogBtnContainer}>
          <div style={styles.mgLeft10}>
            <FlatButton
              style={styles.buttonStyle}
              label=''
              primary
              onClick={this.handleClose}
            />
          </div>
          <div style={styles.mgLeft10}>
            <RaisedButton
              buttonStyle={styles.buttonStyle}
              label=''
              type='sumbit'
              primary
              disabled={!this.state.canSubmit}
            />
          </div>
        </div>
      </Formsy.Form>
    </Dialog>
  };

  renderWallet = (styles) => {
    const cardNumber = <div><span style={styles.walletNumber}>{this.state.yourAddress}</span></div>
    const moneyInfo = this.renderMoneyInfo(styles)
    return <div style={styles.walletInnerContainer}>
      <div style={styles.myWalletContainer}>

        <div style={styles.responsiveWalletContainer}>
          <div style={styles.simpleFlex}>
            <ActionAccountBalanceWallet style={styles.walletImg} />
            <div style={styles.walletInfo}>
              <CardTitle
                title=' '
                subtitle={cardNumber} />

              <CardTitle title=''
                subtitle={moneyInfo}
                style={styles.walletHead} />
            </div>
          </div>
          <div style={styles.buttonContainer}>
            <RaisedButton onClick={this.showPopup} label=' ' primary />
          </div>
        </div>

      </div>
      <div style={styles.transitionHistory}>
        {this.renderTransactionHistory(styles)}
      </div>
    </div>
  };

  renderTransactionHistoryLine = (styles, incoming, money, number, dateTransaction, msgTransaction) => {
    return <div style={styles.transitionHistoryContainer}>

      <div style={styles.senderInfo}>
        <CardText>
          {incoming ? <div style={styles.transferIconContainer}>
            <FileFileDownload style={styles.transferIncomingIcon} />
            <span style={styles.transferIncomingText}></span>
          </div>
            : <div style={styles.transferIconContainer}>
              <FileFileUpload style={styles.transferOutcomingIcon} />
              <span style={styles.transferOutcomingText}></span>
            </div>}
        </CardText>
      </div>
      <div style={styles.transitionSum}>
        {incoming
          ? <ContentAdd style={styles.plusColor} />
          : <ContentRemove style={styles.minusColor} />
        }
        {` ${money}`}
      </div>

          <div style={styles.dateWithNumberContainer}>
        <div style={styles.transitionAddress}>
          <span style={styles.walletNumber}>&nbsp;{number}</span>
        </div>
        <div style={styles.msgContainer}>
        {msgTransaction}
          </div>

        <CardText>
          {dateTransaction}
        </CardText>
      </div>

    </div>
  };

  renderTransactionHistory = (styles) => {
    return <div>
      <div style={styles.transitionHistoryHeadContainer}>
        <div style={styles.transitionHistoryHead}>
          {!!_get(this.state.walletData, 'length') && <CardText> </CardText>}
        </div>
      </div>
      <div style={styles.historyTableContainer}>
        {this.state.walletData.map((object, key) => {
          const {incoming, money, number, dateTransaction, msgTransaction} = object
          return <div key={key}>
            {this.renderTransactionHistoryLine(styles, incoming, money, number, dateTransaction, msgTransaction)}
          </div>
        })}
      </div>
    </div>
  };

  render () {
    return (
      <div style={styles.walletContainer}>
        <Card style={styles.cardStyle}>
          <div style={styles.cardInnerContainer}>
            {this.state.dataLoaded ? <div>{this.renderWallet(styles)}</div>
              : <div style={styles.preloaderCard}>
                <CircularProgress size={80} thickness={5} />
              </div>}
            {this.state.showPreload && <div style={styles.preloaderStyle}>
              <CircularProgress size={80} thickness={5} />
            </div>}
            {this.renderDialog(styles)}
          </div>
        </Card>
      </div>
    )
  }
}

export default Radium(Wallet)



