import Bitcoin from 'bitcoinjs-lib'
import { Buffer } from 'safe-buffer'
import HTTP from 'axios'
import moment from 'moment'
import _find from 'lodash.find'
import _get from 'lodash.get'
import createHash from 'create-hash'

const apiEndpoint = 'http://195.3.252.69:43280'

HTTP.defaults.baseURL = apiEndpoint

const BlockChainAPIEndpoints = {
  getBlock: '/api/block',
  registerWallet: '/api/register',
  getWallet: '/api/address',
  createNewTransaction: '/api/tx/new'
}

const WalletAPI = {
  async createNew () {
    const keyPair = Bitcoin.ECPair.makeRandom()
    console.log('created new keypair:', keyPair)
    const address = keyPair.getAddress()
    console.log('adding to blockchain, address:', address)
    const registerResult = await HTTP.post(BlockChainAPIEndpoints.registerWallet, {address})
    console.log('registerResult1:', registerResult)
    return keyPair.toWIF()
  },

  async _getWalletFTTInfo (address) {
    const walletData = await HTTP.get(`${BlockChainAPIEndpoints.getWallet}/${address}`)
    console.log('walletData:', walletData)
    return _find(walletData.data.info, {cur: 'FTT'})
  },

  _getTransactionBody (address, curBalance, toAddress, amount, message) {
    const txData = {
      from: address,
      to: toAddress,
      amount: amount * 1000000000,
      currency: 'FTT',
      timestamp: moment.unix(new Date()) * 1000,
      seq: curBalance.seq + 1,
      extradata: JSON.stringify({"message":message})
    }
    console.log('tx data', txData)
    const transactionStructure = 'from:to:amount:currency:timestamp:seq:extradata'
    return transactionStructure.split(':').map(key => txData[key].toString()).join(':')
  },

  _packTransaction (walletKeys, transactionBody) {
    try {
      const transactionBodyBuffer = Buffer.from(transactionBody)
      const hash = createHash('sha256').update(transactionBodyBuffer).digest()
      const privateKey = walletKeys.d.toBuffer(32)
      const publicKey = walletKeys.getPublicKeyBuffer()
      const signature = walletKeys.sign(hash).toDER()
      const tx = {
        kLength: publicKey.length,
        sLength: signature.length,
        publicKey: publicKey.toString('base64'),
        signature: signature,
        encodedTransaction: Buffer.from(transactionBody).toString('base64'),
        // not included into transaction
        privateKey: privateKey.toString('base64'),
        hash: hash.toString('base64'),
        rawTransaction: transactionBody
      }
      console.log('tx:', JSON.stringify(tx))
      const lengthsBuffer = Buffer.alloc(2)
      lengthsBuffer.writeUInt8(tx.kLength, 0)
      lengthsBuffer.writeUInt8(tx.sLength, 1)
      const packedTransaction = Buffer.concat([lengthsBuffer, publicKey, signature, transactionBodyBuffer])
      console.log('packedTransaction:', packedTransaction)
      return packedTransaction.toString('base64')
    } catch (e) {
      console.log('_packTransaction error:', e)
    }
  },

  async createNewTransaction (wif, toAddress, amount, message) {
    try {
      const walletKeys = Bitcoin.ECPair.fromWIF(wif)
      const fttBalance = await WalletAPI._getWalletFTTInfo(walletKeys.getAddress())
      console.log("msg",message);
      const transactionBody = WalletAPI._getTransactionBody(walletKeys.getAddress(), fttBalance, toAddress, amount, message)
      console.log('tx body:', transactionBody)
      const packedTransaction = WalletAPI._packTransaction(walletKeys, transactionBody)
      console.log('tx packed:', packedTransaction)
      const transactionResponse = await HTTP.post(BlockChainAPIEndpoints.createNewTransaction, {tx: packedTransaction})
      console.log('tx response:', transactionResponse)
      return transactionResponse
    } catch (e) {
      console.log('createNewTransaction error:', e)
    }
  },

  async loadBalance (wif) {
    const walletKeys = Bitcoin.ECPair.fromWIF(wif)
    const FTTBalance = await WalletAPI._getWalletFTTInfo(walletKeys.getAddress())
    console.log('ftt balance:', FTTBalance)
    return FTTBalance
  },

  async getAddress (wif) {
    const keyPair = Bitcoin.ECPair.fromWIF(wif)
    return keyPair.getAddress()
  },

  _getTransactionHistoryObject (transactionArray, item, address) {
    const checkIncoming = transactionArray[item].from !== address
    const walletNumber = checkIncoming ? transactionArray[item].from : transactionArray[item].to
    const timestampToDate = new Date(transactionArray[item].timestamp / 1000000).toISOString()
    const dateTransaction = moment(timestampToDate.substr(0, 10)).format('DD.MM.YYYY')
    var msgTransaction = '';
    try {
        var extradata=JSON.parse(transactionArray[item].extradata);
        if(typeof(extradata)=="string"){
            //temporary workaround for old badly made transactions
            extradata=JSON.parse(extradata);
        }
        console.log(extradata);
        msgTransaction=extradata.message;
    } catch (e) {
        console.log('_txmsg error:', e)
    }

    return {
      incoming: checkIncoming,
      money: transactionArray[item].amount.toFixed(10).replace(/\.?0+$/, ''),
      number: walletNumber,
      dateTransaction: dateTransaction,
      msgTransaction: msgTransaction
    }
  },

  async _getPreviousBlock (block,address) {
    const getBals = _get(block, 'data.block.bals')
    const balsKey = Object.keys(getBals)
    const lastBlockHash = _get(getBals, `[${address}].FTT.lastblk`)
    return HTTP.get(`${BlockChainAPIEndpoints.getBlock}/${lastBlockHash}`)
  },

  async loadTransactionHistory (currencyState, address) {
    const transactionHistory = []
    let block = await HTTP.get(`${BlockChainAPIEndpoints.getBlock}/${currencyState.lastblk}`)
    for (let blockIndex = currencyState.seq; blockIndex >= 0; blockIndex--) {
      if (blockIndex !== currencyState.seq) block = await WalletAPI._getPreviousBlock(block,address)
      const transactionArray = _get(block, 'data.block.txs')
      const transactionKeys = Object.keys(transactionArray)
      transactionKeys.map((item) => {
        const transactionObject = WalletAPI._getTransactionHistoryObject(transactionArray, item, address)
        transactionHistory.push(transactionObject)
      })
    }
    return transactionHistory.reverse()
  }
}

export default WalletAPI



